<template>
  <header class="el-top-nav">
    <div class="el-btn"  v-bind:class="[isActive ? 'btn-rotate-true' : '']" v-on:click.stop.prevent="mainTransition($event)">
      <div>
        <span class="el-btn-bar"></span>
        <span class="el-btn-bar"></span>
      </div>
    </div>
    <div class="main-icon" @click="goToMain">
      <i class="iconfont icon-zhuyedianji-copy">
      </i>
    </div>
    <div class="top-title">
        {{title}}
    </div>
    <transition name="fadeMask">
      <div class="main-mask" v-show="isMaskShow" @click.stop.prevent="closeMask">
      </div>
    </transition>
    <transition name="fade">
      <div class="main-ul" v-show="isUl">
        <ul>
          <li v-for="(item, index) in Items" @click.stop.prevent="closeMask" class="page-li">
            <router-link :to="{path: item.url}" class="page-a">{{item.title}}</router-link> 
          </li>
        </ul>
      </div>
    </transition>
  </header>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default {
      props: ['articalItems'], // [],{}
      data() {
          return {
              isActive: false,
              isUl: false, // 栏目下拉
              isMaskShow: false,
              Items: this.articalItems
          }
      },
      methods: {
          mainTransition(e) {
            this.isMaskShow = !this.isMaskShow;
            this.isActive = !this.isActive;
            this.isUl = !this.isUl;
          },
          closeMask(e) {
            e.stopPropagation();
            this.isMaskShow = !this.isMaskShow;
            this.isActive = !this.isActive;
            this.isUl = !this.isUl;
          },
          pageUrl(item, index) {
            var index = index + 1;
            this.$router.push({ path: '/mainPage' + index, query: { plan: 'private' }});
          },
          goToMain() {
            this.$router.push({ path: '/mainPage1'});
          }
      },
      computed: { // 扩展，获取标题
        ...mapGetters([ // 相当于
          'title'
        ])
      },
      created() {
        console.log(this.title)
      },
      mounted() {
        console.log(this.title);
      }
  }
</script>
<style scoped>
  body > .el-container {
    margin-bottom: 1rem;
  }

  ul, ul li, p {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  ul {
    padding: 0px 0.5rem 0.5rem 0.5rem;
  }

  .page-li {
    position: relative;
    font-size: 14px;
  }

  .page-li::after {
    position: absolute;
    left: 0;
    bottom: 0;
    content: '';
    height: 1px;
    width: 100%;
    background-color: #eaeaea;
    -webkit-transform: scale(1,.5);
    transform: scale(1,.5);
    -webkit-transform-origin: center bottom;
    transform-origin: center bottom;
}

  .el-top-nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    box-sizing: border-box;
    height: 1.6rem;
    background-color: #fff;
    text-align: center;
    line-height: 1.6rem;
    z-index: 300;
    box-shadow: 0 0.053333rem 0.106667rem rgba(0,0,0,.1);
  }

  .el-btn {
    position: absolute;
    z-index: 1000;
    left: 0.266667rem;
    height: 1.6rem;
    display: block;
  }

  .page-a {
    display: inline-block;
    display: block;
    width: 100%;
    line-height: 1.6rem;
    height: 100%;
  }

  .btn-rotate-true span:nth-child(1) {
    transform-origin: 0 0;
    width: 0.746667rem;
    transform: rotate(45deg);
  }

  .btn-rotate-true span:nth-child(2) {
    transform-origin: 0 0;
    width: 0.746667rem;
    transform: rotate(-45deg);
  }
  
  .el-btn .el-btn-bar {
    display: block;
    margin-top: 0.133333rem;
    transition: all 0.3s;
    width: 0.586667rem;
    height: 1px;
    background: #ea6f5a;
  }

  .el-btn .el-btn-bar:nth-child(2) {
      margin-top: 0.52rem;
  }

  .el-btn div {
    margin-top: 2px;
    padding: 0.266667rem 0.266667rem;
    background: transparent;
    border: none;
  }

  .top-title {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    font-size: 16px;
    font-weight: 600;
  }

  .main-ul {
    position: relative;
    top: 1.6rem;
    bottom: 1.6rem;
    z-index: 1000;
    left: 0;
    right: 0;
    z-index: 600;
    width: 10rem;
    background: #fff;
    color: #333;
    text-align: left;
    box-shadow: 0 0.053333rem 0.106667rem rgba(0,0,0,.1);
  }

  .fade-enter-active, .fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: opacity 1s;
  }

  .fade-enter,.fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    transform: translateY(0px);
    opacity: 0;
  }

  .fadeMask-enter-active, .fadeMask-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: opacity 1s;
  }

  .fadeMask-enter,.fadeMask-leave-to /* .fade-leave-active below version 2.1.8 */ {
    transform: translateY(0px);
    opacity: 0;
  }

  .main-mask {
    position: fixed;
    top: 1.6rem;
    bottom: 0px;
    left: 0px;
    right: 0px;
    content: "";
    background: #000;
    opacity: 0.7;
  }

  .main-icon {
    position: absolute;
    font-size: 24px;
    width: 1.066667rem;
    right: 0.533333rem;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10000;
  }
</style>


