<template>
  <div id="app">
  	<top-nav v-bind:articalItems="articalItems"></top-nav>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <scroll-top v-show="isGoShow" @goToTop="mainSetGo"></scroll-top>
    <footer-page></footer-page>
  </div>
</template>

<script>
  import scrollTop from './components/scrollTop.vue';
  import topNav from './components/topNav.vue';
  import footer from './components/footer.vue';
	export default {
		data() {
      return {
          isGoShow: false, // 回到顶部,
          articalItems: [
            {"title": "首页", "url": "/mainPage1"},
            {"title": "二叶", "url": "/mainPage2"},
            {"title": "三叶", "url": "/mainPage3"},
            {"title": "弹性布局", "url": "/flex"},
            {"title": "瀑布流布局", "url": "/pubu"},
          ]
      }
    },
    methods: {
       mainSetGo(data) {
          this.isGoShow = data;
        },
    },
		components: {
		    'top-nav': topNav,
        'scroll-top': scrollTop,
        'footer-page': footer
		}
	}
</script>

<style scoped>
</style>



